*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Newsreader', 'Roboto', sans-serif;
  line-height: 1.5;
  color: #181717;
  background-color: #f0dfb3;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 20px;
  margin-bottom: 25px;
}

.btn {
  width: 150px;
  background-color: #a6e7a8;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;

  font-size: 20px;
  font-weight: 600;
  border-radius: 8px;
  border-color: #f1e4dd;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.button.is-hidden {
  display: none;
}

.button > .spinner.is-hidden {
  display: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input {
  font: 'Newsreader', inherit;
  margin-right: 20px;
  width: 400px;
  font-size: 20px;
  font-weight: 300;
  align-items: center;

  border-radius: 8px;
  border-color: #a6e7a8;
}

.stats {
  display: flex;
  justify-content: space-around;
}

.stats-item {
  display: flex;
  justify-content: space-around;
  background-color: #e78982;
  border-radius: 6px;
  padding: 1px;
}

.material-icons {
  color: #f1e4dd;
  margin-right: 5px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.gallery img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.gallery a {
  text-decoration: none;
  color: #2a2a2a;
}

.articles li {
  height: 450px;
  overflow: hidden;
}

.articles img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.articles a {
  text-decoration: none;
  color: #2a2a2a;
}